<template>
  <div>
    <van-nav-bar
      title="我的收藏"
      left-arrow
      @click-left="$router.push('/myjp')"
    ></van-nav-bar>

    <div class="item-empty" v-show="bool">
      <img src="https://jp.juancdn.com/jpwebapp_v1/images_v1/empty_icons/user_fav.png?932988a3-1&sv=449ce9ee" alt />
      <div class="b-title">看到喜欢的商品就收藏吧</div>
      <div class="btnBc" @click="$router.push('/home')">今日推荐</div>
    </div>



    <div class="shopping" v-for="(item,index) in collection" :key="index" @click="$router.push({path:'/details',query:{ index: index}})">
      <van-card
        :price="item.originalPrice"
        desc="描述信息"
        :title="item.describe"
        :thumb="item.url"
      />
    </div>
  </div>
</template>

<script>
export default {
    data(){
       return {
        collection:[]
       }
    },
    created(){
         this.$store.state.bool = false;
         this.collection = this.$store.state.classify.Single.filter(item=>item.active)
    },
    computed:{
      bool() {
      return this.collection.length < 1;
    }
    }
};
</script>

<style lang="scss" scoped>
.item-empty {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%,-50%);
  img {
    display: block;
    margin: 0 auto;
    margin-bottom: 12px;
    width: 140px;
    height: auto;
  }
  .b-title {
    font-size: 16px;
    color: #333;
    margin-bottom: 18px;
  }
  .btnBc {
    width: 138px;
    height: 40px;
    margin: 0 auto;
    line-height: 40px;
    font-size: 15px;
    color: #ff464e;
    border: 1px solid currentColor;
    text-align: center;
  }
}
</style>